<template>
    <div>
        <basic-header :items="['店铺管理','商品管理']"></basic-header>
        <el-card style="margin-top: 20px">
            <template>
                <el-tabs v-model="activeName" @tab-click="handleType">
                    <el-tab-pane label="商品查看" name="first">
                        <el-form inline label-width="150px">
                            <el-form-item label="名称">
                                <el-input v-model="searchForm.name" :clearable="true"></el-input>
                            </el-form-item>
                            <el-form-item label="库存状态">
                                <template>
                                    <el-select v-model="searchForm.store" :clearable="true" placeholder="请选择">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </template>
                            </el-form-item>
                            <el-form-item label="更新时间">
                                <el-date-picker
                                        v-model="searchForm.searchTime"
                                        type="daterange"
                                        range-separator="至"
                                        start-placeholder="开始日期"
                                        end-placeholder="截止日期">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="search" circle
                                           icon="el-icon-search circle"></el-button>
                            </el-form-item>
                        </el-form>
                        <el-table :data="tableData">
                            <el-table-column prop="id" label="ID"></el-table-column>
                            <el-table-column prop="name" label="名称"></el-table-column>
                            <el-table-column prop="catname" label="分类"></el-table-column>
                            <el-table-column prop="description" label="描述"></el-table-column>
                            <el-table-column prop="thumb" label="缩略图">
                                <template #default="o">
                                    <img :src="o.row.thumb" alt="" width="100" height="100">
                                </template>
                            </el-table-column>
                            <el-table-column prop="price" label="价格">
                            </el-table-column>
                            <el-table-column prop="store" label="库存"></el-table-column>
                            <el-table-column prop="weight" label="重量"></el-table-column>
                            <el-table-column prop="create_time" label="创建时间">
                                <template #default="o">
                                    <span v-time="(o.row.create_time)"></span>
                                </template>
                            </el-table-column>
                            <el-table-column prop="update_time" label="更新时间"></el-table-column>
                            <el-table-column prop="sort" label="排序"></el-table-column>
                            <el-table-column prop="state" label="上架状态">
                                <template #default="obj">
                                    <el-button type="primary" v-if="obj.row.state===1" size="mini" round>上架</el-button>
                                    <el-button type="error" v-else="" size="mini" round>下架</el-button>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作">
                                <template #default="o">
                                    <el-button type="text" @click="edit(o.row.id)">编辑</el-button>
                                    <el-button type="text" @click="del(o.row.id)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-pagination
                                background
                                style="margin-top: 20px"
                                layout="prev, pager, next"
                                :total=total
                                :page-size.sync=pageSize
                                :current-page.sync=page
                        >
                        </el-pagination>
                    </el-tab-pane>
                    <el-tab-pane label="商品添加" name="second">
                        <el-steps :active="active" finish-status="success">
                            <el-step title="基本信息"></el-step>
                            <el-step title="详细信息"></el-step>
                            <el-step title="配送信息"></el-step>
                        </el-steps>
                        <el-form label-width="100px" :model="typeForm" ref="typeForm" :rules="typeRules">
                            <el-col v-show="active==0">
                                <el-form-item label="分类" prop="tid">
                                    <el-select v-model="typeForm.tid" :clearable="true" placeholder="请选择商品">
                                        <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                                   :label="item.name"></el-option>
                                    </el-select>
                                    <router-link :to="{name:'type'}">添加分类</router-link>
                                </el-form-item>
                                <el-form-item label="商品名称" prop="name">
                                    <el-input v-model="typeForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="商品介绍" prop="description">
                                    <el-input v-model="typeForm.description"></el-input>
                                </el-form-item>
                                <el-form-item label="商品价格" prop="price">
                                    <el-input v-model="typeForm.price"></el-input>
                                </el-form-item>
                                <el-form-item label="商品库存" prop="store">
                                    <el-input v-model="typeForm.store"></el-input>
                                </el-form-item>
                                <el-form-item label="商品排序" prop="sort">
                                    <el-input v-model="typeForm.sort"></el-input>
                                </el-form-item>
                                <el-form-item label="商品重量" prop="weight">
                                    <el-input v-model="typeForm.weight"></el-input>
                                </el-form-item>
                                <el-form-item label="缩略图" prop="thumb">
                                    <el-upload
                                            class="avatar-uploader"
                                            :action="uploadUrl"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="typeForm.thumb" :src="typeForm.thumb" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                    <el-button type="primary" @click="handlethumbRemove">取消上传</el-button>
                                </el-form-item>
                                <el-form-item label="商品banner" prop="banner">
                                    <el-upload
                                            ref="duotu"
                                            class="upload-demo"
                                            :action="uploadUrl"
                                            :before-upload="beforeAvatarUpload"
                                            :on-remove="handleBannerRemove"
                                            :on-success="handleBannerSuccess"
                                            list-type="picture">
                                        <el-button size="small" type="primary">点击上传</el-button>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col v-show="active==1">
                                <el-form-item prop="content">
                                    <div ref="editor" id="test"></div>
                                </el-form-item>
                            </el-col>
                            <el-col v-show="active==2">
                                <el-form-item label="商品配送" prop="delivery_id">
                                    <el-select v-model="typeForm.delivery_id" :clearable="true" placeholder="请选择配送模板">
                                        <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id"
                                                   :label="item.name">
                                        </el-option>
                                    </el-select>

                                </el-form-item>
                                <el-form-item>
                                    <el-switch
                                            v-model="typeForm.state"
                                            active-text="上架"
                                            inactive-text="下架"
                                            :active-value="1"
                                            :inactive-value="0"
                                    >
                                    </el-switch>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="handleSubmit">提交</el-button>
                                </el-form-item>
                            </el-col>
                        </el-form>
                        <el-button-group>
                            <el-button type="primary" icon="el-icon-arrow-left" :disabled="active==0" @click="active--">
                                上一页
                            </el-button>
                            <el-button type="primary" @click="active++" :disabled="active==2">下一页<i
                                    class="el-icon-arrow-right el-icon--right"></i></el-button>
                        </el-button-group>
                    </el-tab-pane>
                </el-tabs>
            </template>
            <el-dialog
                    title="提示"
                    :visible.sync="dialogVisible"
                    width="80%"
            >
                <el-form label-width="80px" ref="editorForm" :model="editorForm" :rules="typeRules">
                    <el-col :span="12">
                        <el-form-item label="商品分类" prop="tid">
                            <el-select v-model="editorForm.tid" :clearable="true" placeholder="请选择商品">
                                <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="商品名称" prop="name">
                            <el-input v-model="editorForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="商品介绍" prop="description">
                            <el-input v-model="editorForm.description"></el-input>
                        </el-form-item>
                        <el-form-item label="商品价格" prop="price">
                            <el-input v-model="editorForm.price"></el-input>
                        </el-form-item>
                        <el-form-item label="商品库存" prop="store">
                            <el-input v-model="editorForm.store"></el-input>
                        </el-form-item>
                        <el-form-item label="商品排序" prop="sort">
                            <el-input v-model="editorForm.sort"></el-input>
                        </el-form-item>
                        <el-form-item label="商品重量" prop="weight">
                            <el-input v-model="editorForm.weight"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="商品缩略图" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="uploadUrl"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess1"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="editorForm.thumb" :src="editorForm.thumb" class="avatar">
                                <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="banner图">
                            <el-upload
                                    class="upload-demo"
                                    :action="uploadUrl"
                                    :before-upload="beforeAvatarUpload"
                                    :on-remove="handleBannerRemove1"
                                    :on-success="handleBannerSuccess1"
                                    :file-list="fileList"
                                    list-type="picture">
                                <el-button size="small" type="primary">点击上传</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item prop="content">
                            <div ref="editor2"></div>
                        </el-form-item>
                        <el-form-item>
                            <el-switch
                                    v-model="editorForm.state"
                                    active-text="上架"
                                    inactive-text="下架"
                                    :active-value="1"
                                    :inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                        <el-form-item label="配送模板">
                            <el-select v-model="editorForm.delivery_id" :clearable="true" placeholder="请选择配送模板">
                                <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id"
                                           :label="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-form>
                <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editorSubmit">确 定</el-button>
  </span>
            </el-dialog>
        </el-card>
    </div>

</template>

<script>
    import MBXhead from "../../components/MBXhead";
    import formatDate from "../../utils/formatDate";
    import wangeditor from "wangeditor"

    export default {
        data() {
            return {
                api: "/api/goods/goods",
                activeName: "first",
                tableData: [],
                page: 1,
                pageSize: 5,
                total: 0,
                options: [{
                    value: '0',
                    label: '库存为空'
                }, {
                    value: '1',
                    label: '库存正常'
                }],
                searchForm: {
                    name: "",
                    store: "",
                    searchTime: ""
                },
                active: 0,
                typeOption: [],
                typeForm: {
                    tid: "",
                    name: "",
                    description: "",
                    price: "",
                    store: "",
                    sort: "",
                    thumb: "",
                    banner: "",
                    content: "",
                    delivery_id: "",
                    state: "",
                    weight: ""
                },
                uploadUrl: "/api/upload/upload",
                typeRules: {
                    tid: {required: true, message: '请输入产品名称', trigger: 'change'},
                    name: {required: true, message: '请输入产品名称', trigger: 'blur'},
                    description: {required: true, message: '请输入产品名称', trigger: 'blur'},
                    price: {required: true, message: '请输入产品价格', trigger: 'blur'},
                    store: {required: true, message: '请输入产品库存状态', trigger: 'blur'},
                    sort: {required: true, message: '请输产品排序', trigger: 'blur'},
                    thumb: {required: true, message: '请上传图片'},
                    banner: {required: true, message: '请上传图片'},
                    content: {required: true, message: '请输内容', trigger: 'blur'},
                    delivery_id: {required: true, message: '请输入产品名称', trigger: 'change'},
                    weight: {required: true, message: '请输商品重量', trigger: 'blur'}
                },
                editor: "",
                deliveryOption: [],
                editorForm: {},
                dialogVisible: false,
                fileList: []
            }
        },
        methods: {
            fetchData() {
                let params = {
                    page: this.page,
                    pageSize: this.pageSize
                }
                if (this.searchForm.name) {
                    params['name'] = this.searchForm.name
                }
                if (this.searchForm.store) {
                    params['store'] = this.searchForm.store
                }
                if (this.searchForm.searchTime) {
                    let start = this.searchForm.searchTime[0].getTime();
                    let end = this.searchForm.searchTime[1].getTime();
                    params['start_time'] = Math.floor(start / 1000);
                    params['end_time'] = Math.floor(end / 1000);
                }
                this.axios.get(this.api, {params}).then(res => {
                    if (res.data.code === 200) {
                        // console.log(res);
                        this.tableData = res.data.data;
                        this.total = res.data.total;
                    }
                })
            },
            search() {
                this.fetchData()
            },
            del(id) {
                this.$confirm('此操作将永久删除该分类，是否继续？', '提示', {
                    confirmButtonText: "取消",
                    confirmButtonText: "确定",
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then((res) => {
                        if (res.data.code == 200) {
                            this.$message.success("删除成功");
                            this.fetchData()
                        } else {
                            this.error(res.data.msg)
                        }
                    })
                }).catch(() => {
                    this.$message.info("已取消删除")
                });
            },
            selectType() {
                this.axios.get("/api/type/type").then((res) => {
                    this.typeOption = res.data.data
                })
            },
            selectDelivery() {
                this.axios.get("/api/delivery/delivery").then((res) => {
                    this.deliveryOption = res.data.data
                })
            },
            handleType() {
                if (this.activeName === "second") {
                    if (this.typeOption.length == 0) {
                        this.selectType();
                        this.selectDelivery()
                    }
                }
            },
            handleAvatarSuccess(res) {
                this.typeForm.thumb = res;
                this.$refs.typeForm.validateField("thumb");
            },
            handleAvatarSuccess1(res) {
                this.editorForm.thumb = res;
                this.$refs.editorForm.validateField("thumb");
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt500kb = file.size / 1024 / 500;

                if (!isJPG) {
                    this.$message.error('上传图片只能是 JPG 格式!');
                }
                if (!isLt500kb) {
                    this.$message.error('上传图片大小不能超过 500kb');
                }
                return isJPG && isLt500kb;
            },
            handlethumbRemove() {
                this.typeForm.thumb = "";
            },
            handleBannerSuccess(response, file, fileList) {
                this.typeForm.banner = fileList.map(v => v.response).join(";")
                this.$refs.typeForm.validateField("banner");
            },
            handleBannerSuccess1(response, file, fileList) {
                this.editorForm.banner = fileList.map(v => v.response).join(";")
                this.$refs.editorForm.validateField("banner");
            },
            handleBannerRemove(file, fileList) {
                this.typeForm.banner = fileList.map(v => v.response).join(";")
            },
            handleBannerRemove1(file, fileList) {
                this.editorForm.banner = fileList.map(v => v.response).join(";")
            },
            handleSubmit() {
                if (this.editor.txt.html() !== "<p><br></p>") {
                    this.typeForm.content = this.editor.txt.html();
                }
                this.$refs.typeForm.validate(valid => {
                    if (valid) {
                        this.axios.post(this.api, this.typeForm).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("添加成功");
                                this.$refs.typeForm.resetFields();
                                this.$refs.duotu.clearFiles();
                                this.active = 0;
                                this.activeName = "first";
                                this.editor.txt.clear();
                                this.fetchData();
                            }
                        })
                    } else {
                        this.$message.error("验证失败")
                    }
                })

            },
            edit(id) {
                this.dialogVisible = true;
                this.axios.get(this.api, {params: {id: id}}).then(res => {
                    // console.log(res);
                    this.editorForm = res.data.data;
                    this.fileList = this.editorForm.banner.split(";").map((v, i) => ({name: i + 1 + ".jpg", url: v}));
                    if (this.typeOption.length == 0) {
                        this.selectType();
                        this.selectDelivery()
                    };
                    this.$nextTick(() => {
                        this.editor2 = new wangeditor(this.$refs.editor2);
                        this.editor2.customConfig.uploadImgServer = "/api/upload/editorupload";
                        this.editor2.customConfig.uploadFileName = "file";
                        this.editor2.create();
                        this.editor2.txt.html(this.editorForm.content);
                       });
                })
            },
            editorSubmit(){
                if (this.editor2.txt.html() !== "<p><br></p>") {
                    this.editorForm.content = this.editor2.txt.html();
                }
                this.$refs.editorForm.validate(valid => {
                    if (valid) {
                        delete this.editorForm.create_time;
                        this.axios.put(this.api, this.editorForm).then(res => {
                            if (res.data.code === 200) {
                                this.dialogVisible=false
                                this.fetchData();
                            }
                        })
                        this.$message.success("修改成功")
                    } else {
                        this.$message.error("验证失败.请检查字段内容")
                    }
                })
            }
        },
        components: {
            "basic-header": MBXhead
        },
        directives: {
            time: {
                inserted(el, bind) {
                    el.innerHTML = formatDate(bind.value)
                }
            }
        },
        watch: {
            page: function () {
                this.fetchData()
            },
            active(value) {
                if (value === 1 && this.editor === "") {
                    //当值修改后，dom也相对应的完成一次更新后才开始执行此回调函数
                    this.$nextTick(() => {
                        this.editor = new wangeditor(this.$refs.editor);
                        this.editor.customConfig.uploadImgServer = "/api/upload/editorupload";
                        this.editor.customConfig.uploadFileName = "file";
                        this.editor.create();
                    })
                }
            },
        },
        created() {
            this.fetchData()
        },
        mounted() {

        },
    }
</script>
<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>